import photo from '../img/photo.jpg';
import vk from '../img/vk.svg';
import telegram from '../img/telegram.svg';
import linkedin from '../img/linkedin.svg';
import git from '../img/git.svg';
import { SocialNetwork } from '../components/SocialNetwork';

function Home() {
  return (
    <section className="home" id="home">
      <div className="home_img">
        <img src={photo} alt="My photo" />
      </div>
      <div className="home_content">
        <h5 className="home_logo">My Portfolio</h5>
        <div>
          <h4>
            <span>!</span>  
          </h4>
          <h2> </h2>
          <h4> Junior Frontend - developer</h4>
          <p>
                 React, 
                ,   
                   . 
               ,    .
                 . <br />  
             ,      ,
             .
          </p>
          <a href="#about" className="home_btn">
             
          </a>
        </div>
        <ul className="home_social">
          <SocialNetwork social_image={linkedin} social_link='https://www.linkedin.com/in/denis-dudin-7b54a522a/'/>
          <SocialNetwork social_image={vk} social_link='https://vk.com/d0bry_den'/>
          <SocialNetwork social_image={telegram} social_link='https://t.me/D0bryDen'/>
          <SocialNetwork social_image={git} social_link='https://github.com/DenisDudin'/>
        </ul>
      </div>
    </section>
  );
}

export { Home };
